/***
	Styling (CSS) of all Vue components and HTML elements on the web.
**/
html {
  height: 100;
  width: 100%;
  overflow: hidden;
  overflow-y: scroll;
  overflow-x: scroll; }
html, body {
  height: 100%;
  margin: 0; }
body {
  margin: 0; }
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
*:focus {
    outline: none; }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background-color: #E9E9ED;
  margin: 0; }
#navigation-bar {
  text-align: left;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
  padding: 2vh 3vw; }
#navigation-bar .routerlink {
    color: black;
    text-decoration: none;
    margin-right: 2vw; }
#navigation-bar .routerlink:hover {
      color: red; }
#navigation-bar img {
    border: 1px solid red; }
#home {
  background: url(/static/img/cloud.06f3b68.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  overflow: scroll; }
#home #upper-panel {
    height: 80%;
    width: 100%;
    position: relative;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out; }
#home #left-upper-panel {
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
    width: 100%;
    position: relative;
    height: 100%;
    float: left;
    text-align: center; }
#home #right-upper-panel {
    width: 50%;
    position: relative;
    height: 100%;
    float: right; }
#home #lower-panel {
    height: 20%;
    width: 100%;
    position: relative; }
#home #lower-panel .fade-enter-active {
      -webkit-transition: opacity 0.5s ease-in-out;
      transition: opacity 0.5s ease-in-out; }
#home #lower-panel .fade-enter,
    #home #lower-panel .fade-leave-to {
      opacity: 0; }
#search-panel {
  position: absolute;
  top: 30%;
  width: 100%; }
#search-panel .search-input {
    width: 80%;
    border: 0.15vw solid grey;
    border-style: inset;
    border-radius: 0.3vw;
    font-size: 1.8vh;
    padding: 1.5vh 0 1.5vh 5vh;
    background-color: white;
    background-image: url(data:image/png;base64,);
    background-size: 3vh 60%;
    background-position: 0.7vw 1vh;
    background-repeat: no-repeat; }
#search-panel .list-search-result {
    position: absolute;
    left: 10%;
    z-index: 999;
    width: 80%;
    max-height: 50vh;
    overflow-y: scroll;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
    background-color: #1C1C1D;
    color: white; }
#search-panel .list-search-result .element-list {
      padding: 1vh 2vw;
      text-align: left;
      cursor: pointer; }
#search-panel .list-search-result .element-list:hover {
        background-color: lightgray;
        color: black; }
#recent-location-panel {
  height: 80%;
  position: absolute;
  top: 10%;
  right: 5%;
  width: 80%;
  border-collapse: collapse;
  padding: 0.25rem;
  border-style: hidden; }
#recent-location-panel #list-location {
    border: 2px solid gray;
    border-style: groove;
    overflow: scroll;
    height: 90%; }
#recent-location-panel h3 {
    color: black;
    margin: 1vh auto;
    font-size: 2.5vh; }
#recent-location-panel .element-location {
    background-color: black;
    opacity: 0.8;
    margin: 1vh;
    padding: 1.5vh 1vw;
    color: white;
    font-size: 100%; }
#recent-location-panel .element-location:hover {
      cursor: pointer; }
#recent-location-panel .element-location .fa:hover {
      color: red; }
#recent-location-panel .ghost {
    opacity: 0.4;
    background: black; }
#weather-display-panel {
  position: absolute;
  width: 95%;
  height: 90%;
  top: 5%;
  left: 2.5%;
  border-style: hidden; }
#weather-display-panel .table {
    display: table;
    width: 100%;
    border-collapse: collapse; }
#weather-display-panel .columns {
    display: table-row;
    border-style: groove;
    opacity: 0.9; }
#weather-display-panel #forecast-element {
    display: table-cell;
    border: 1px solid white;
    background-color: #141415;
    color: white;
    text-align: center;
    padding: 0.5vh 1vw; }
#weather-display-panel #forecast-element p {
      font-size: 1.8vh; }
#weather-display-panel #forecast-element h4, #weather-display-panel #forecast-element h5 {
      margin: 1vh; }
legend {
  font-weight: bold;
  color: white;
  background-color: #1C1C1D;
  border: 2px solid grey;
  border-style: groove;
  padding: 0 1vw; }
